import { config } from "../../config/firebase"
import axios from 'axios';
import { put, call } from 'redux-saga/effects';
import * as actions from '../actions/buku'

const API_KEY = config["apiKey"];

export function* tambahBuku(action) {

  const {judul, lokasiTahun, isbn, penerbit} = action.edit;

  const localId = localStorage.getItem("localId");
  try{

    const urlDB = `https://repository-1d687.firebaseio.com/buku.json`

    yield axios.post(urlDB, {judul, lokasiTahun, isbn, penerbit, penulis: localId })


    // nik: nik => nik

  } catch (error){
    console.log(error);
  }

}

export function* lihatBuku(){
  const localId         = localStorage.getItem("localId");
  const urlDBBuku       = `https://repository-1d687.firebaseio.com/buku.json?orderBy="penulis"&equalTo="${localId}"`;


try{
  const resDBBuku       = yield axios.get(urlDBBuku);



    yield put(actions.lihatBukuSukses(resDBBuku.data));

  // const resDBPenelitian = yield axios.get(urlDBPengabdian);
  // const { abstrak, file, judul, penulis, tipe } = resDBPenelitian.data || {};

}catch (error){
  console.log(error);

}

}

export function* editBuku(action){
  const localId         = localStorage.getItem("localId");

  const editDataBuku = {
    judul: action.edit.judul,
    lokasiTahun: action.edit.lokasiTahun,
    isbn: action.edit.isbn,
    penerbit: action.edit.penerbit,
    penulis: localId,
  }

  const urlDB = `https://repository-1d687.firebaseio.com/buku/${action.edit.idBuku}.json`;



  try{
    const resDB = yield axios.put(urlDB, editDataBuku);

  }catch(error){
    console.log(error);
  }
}

export function* hapusBuku(action){
  const urlDB = `https://repository-1d687.firebaseio.com/buku/${action.idBuku}.json`;

  try{
    const resDB = yield axios.delete(urlDB);
    yield put(actions.lihatBuku());
  }catch(error){
    console.log(error);
  }
}
