import { updateState } from "../utils/updateState";

const initialState = {
  daftarBuku: null,

};

const lihatBukuSukses = (state, action) =>{
  return updateState(state, {daftarBuku: action.daftarBuku }  )
}

const reducer = (state = initialState, action) =>{
  switch (action.type) {
    case "BUKU_LIHAT_BUKU_SUKSES":
    return lihatBukuSukses(state, action);



    default: return state;

  }

};




export default reducer;
